body {
  margin: 0;
  padding: 0;
}

.cards-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .cards-container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .cards-container {
    --column-count: 4;
  }
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-top: 4px;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s ease-in-out;
}

.is-active {
  color: #00acdc;
  display: block;
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  top: 1px;
  left: 1px;
}

.card_image {
  width: 160px;
  height: 134px;
  padding: 32px 19px;
}

.card_content {
  padding: 0 16px;
}

.card_title {
  color: #060b35;
  margin: 4px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card_title:hover {
  color: #34568b;
  transition: all .3s ease-in-out;
}

.card_product_code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card_reviews {
  justify-content: space-between;
  margin: 16px 0 24px;
  display: flex;
}

.card_review {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card_product_price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card_text_price {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card_price {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card_button_buy {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: 0;
  border-radius: 5px;
  outline: none;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
}

.card_button_buy:hover {
  color: #00acdc;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #00acdc;
  transition: all .3s ease-in-out;
}
